import React from "react";
import Typography from "@material-ui/core/Typography";
import { connect } from "react-redux";
import { changeLoginDisplay } from "../Redux/LoginState/actions";

const mapDispatchToProps = (dispatch) => {
  return {
    setShowLogin: (display) => dispatch(changeLoginDisplay(display)),
  };
};

const mapStateToProps = (state) => {
  return {
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
  };
};

const NeedToLogin = ({ setShowLogin }) => {
  return (
    <div
      style={{
        height: "100%",
        width: "100%",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <Typography>
        Need to{" "}
        <strong onClick={() => setShowLogin(true)} className="App-link">
          Login
        </strong>
      </Typography>
    </div>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(NeedToLogin);
