import {
  CHANGE_SEARCH_VALUE,
  SEARCH_STOCKS_PENDING,
  SEARCH_STOCKS_SUCCESS,
  SEARCH_STOCKS_FAILED,
  CHANGE_SEARCHED_STOCKS,
} from "./appConstants.js";

const initialStateSearch = {
  searchValue: "",
};

export const search = (state = initialStateSearch, action = {}) => {
  switch (action.type) {
    case CHANGE_SEARCH_VALUE:
      return Object.assign({}, state, { searchValue: action.payload });
    default:
      return state;
  }
};

const initialSearchedStocks = {
  isPending: false,
  error: "",
  stocks: [],
};

export const requestSearchedStocks = (
  state = initialSearchedStocks,
  action = {}
) => {
  switch (action.type) {
    case SEARCH_STOCKS_PENDING:
      return Object.assign({}, state, { isPending: true });
    case SEARCH_STOCKS_SUCCESS:
      return Object.assign({}, state, {
        stocks: action.payload,
        isPending: false,
      });
    case SEARCH_STOCKS_FAILED:
      return Object.assign({}, state, {
        error: action.payload,
        isPending: false,
      });
    default:
      return state;
  }
};
