import React from "react";
import "./MenuItem.css";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";

const MenuItem = ({
  hover,
  symbol,
  name,
  price,
  change,
  percentChange,
  ...others
}) => {
  const addHover = () => {
    if (hover) {
      return "hoverItem";
    }
    return "";
  };

  const displayPrice = () => {
    if (price) {
      return (
        <Grid item xs={5}>
          <div className="rightItems">
            <Typography variant="inherit" display="inline" align="right" noWrap>
              <strong className="symbol">{price}</strong>
            </Typography>
            <Typography display="inline" align="right" variant="inherit" noWrap>
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <span className="name">{change.toFixed(2)}</span>
                <span className="name">{percentChange.toFixed(2) + "%"}</span>
              </div>
            </Typography>
          </div>
        </Grid>
      );
    }
  };

  return (
    <span {...others}>
      <div className={` MenuItem ` + addHover()}>
        <Grid container wrap="nowrap" spacing={2}>
          <Grid item xs={7} zeroMinWidth>
            <Typography display="inline" variant="inherit" align="left" noWrap>
              <p className="symbol"> {symbol}</p>
            </Typography>

            <Typography display="inline" variant="inherit" align="left" noWrap>
              <p className="name"> {name}</p>
            </Typography>
          </Grid>
          {displayPrice()}
        </Grid>
      </div>
    </span>
  );
};

export default MenuItem;
