import { CHANGE_LOGIN_DISPLAY, CHANGE_LOGIN_STATUS } from "./constants";

const initLoginState = {
  showLogin: false,
};

export const loginState = (state = initLoginState, action = {}) => {
  switch (action.type) {
    case CHANGE_LOGIN_DISPLAY:
      return { ...state, showLogin: action.payload };
    default:
      return state;
  }
};

const initLoginStatus = {
  userIsLoggedIn: false,
};

export const loginStatus = (state = initLoginStatus, action = {}) => {
  switch (action.type) {
    case CHANGE_LOGIN_STATUS:
      return { ...state, userIsLoggedIn: action.payload };
    default:
      return state;
  }
};
