import React, { useState, useEffect } from "react";
import MenuItem from "../Components/MenuItem";
import NotificationsIcon from "@material-ui/icons/Notifications";
import AddAlertIcon from "@material-ui/icons/AddAlert";
import BookmarkTwoToneIcon from "@material-ui/icons/BookmarkTwoTone";
import StockQuote from "../Components/StockQuote";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import IconButton from "@material-ui/core/IconButton";
import getETHours from "../UsefullFunctions/getETHours";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ShowChartIcon from "@material-ui/icons/ShowChart";

const isStockOpen = () => {
  let time = new Date();

  let hr = getETHours(time);

  const verifyDate = (time) => {
    let day = time.getUTCDay();
    let date = time.getUTCDate();

    if (day === 6) {
      date = date - 1;
    } else if (day === 0) {
      date = date - 2;
    }

    date = date + "/" + time.getUTCMonth();
    return date;
  };

  if (hr >= 9 && hr <= 16) {
    return (
      <Typography variant="subtitle2" align="left">
        Open {verifyDate(time)} 09:00 EDT
      </Typography>
    );
  }
  return (
    <Typography variant="subtitle2" align="left">
      Closed {verifyDate(time)} 16:00 EDT
    </Typography>
  );
};

const StockInfoBar = ({ stockDetails }) => {
  return (
    <div>
      <StockQuote />
      <Card>
        <CardContent> {isStockOpen()}</CardContent>{" "}
      </Card>

      <div>
        <Card>
          <CardActions>
            <IconButton aria-label="add to watchlist">
              <BookmarkTwoToneIcon />
            </IconButton>

            <IconButton aria-label="alerts">
              <AddAlertIcon />
            </IconButton>
            <Button
              variant="contained"
              color="default"
              startIcon={<ShowChartIcon />}
            >
              {" "}
              Trade
            </Button>
          </CardActions>
        </Card>
        <Card>
          <CardHeader title="Company Financials" subheader="" />
          <CardContent>
            <Grid container>
              <Grid item xs>
                <Typography variant="subtitle1">Stuff</Typography>
              </Grid>
              <Grid item xs>
                <Typography variant="subtitle1">More Stuff</Typography>
              </Grid>
              <Grid item xs>
                <Typography variant="subtitle1"> . . .</Typography>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default StockInfoBar;
