import {
  USER_DATA_PENDING,
  USER_DATA_SUCCESS,
  USER_DATA_FAILED,
  DELETE_USER_DATA,
} from "./constants";

import {
  ADD_WATCHLIST_SUCCESS,
  ADD_WATCHLIST_PENDING,
  ADD_WATCHLIST_FAILED,
  ADD_SYMBOL_PENDING,
  ADD_SYMBOL_FAILED,
  DELETE_WATCHLIST_FAILED,
  DELETE_WATCHLIST_PENDING,
  DELETE_WATCHLIST_SUCCESS,
  ADD_SYMBOL_SUCCESS,
  REMOVE_SYMBOL_FAILED,
  REMOVE_SYMBOL_PENDING,
  REMOVE_SYMBOL_SUCCESS,
} from "../../WebPages/Watchlist/constants";

import {
  CREATE_EVENT_FAILED,
  CREATE_EVENT_SUCCESS,
  ACTIVATE_EVENT,
  DEACTIVATE_EVENT,
  DELETE_EVENT_SUCCESS,
  DELETE_EVENT_FAILED,
} from "../Events/constants";

import {
  ACCOUNT_DEPOSIT_SUCCESS,
  ACCOUNT_DEPOSIT_FAILED,
  ACCOUNT_WITHDRAWAL_SUCCESS,
  ACCOUNT_WITHDRAWAL_FAILED,
} from "../Account/constants";

import {
  createNewWatchlist,
  deleteWatchlist,
  getWatchlist,
  removeWatchlistSymbol,
} from "../../Server Sync Fns/watchlistSync";

import {
  createEvent,
  getEvent,
  deleteEvent,
} from "../../Server Sync Fns/eventSync";

import { useEffect } from "react";

import { accountDeposit, accountWithdrawal } from "../Account/actions";
import { requestUserData } from "./actions";

import merge from "lodash/merge";
const initUserData = {
  userData: {
    username: "",
    adminType: "none",
    watchlists: [
      {
        name: "default",
        symbols: ["TSLA", "AMZN", "AAPL"],
      },
    ],
    stockCrawler: [],
    events: {
      alerts: [],
      events: [],
    },
    portfolio: {
      account_value: 0,
      active_orders: [],
      owned_stocks: [],
      traded_stocks: [],
      stocks_PnL: 0,
      history: {
        deposits: [],
        withdrawals: [],
        orders: [],
        daily: [],
      },
    },

    Robotrade_portfolio: {
      trading_style: "Aggressive",
      deposit: 0,
      amount_allocated: 0,
      net_value: 0,
      profit_or_loss: 0,
      trade_with_profits: "no",
      orders: [],
      stock_to_trade: [],
      traded_stocks: [],
      history: {
        deposits: [],
        withdrawals: [],
        orders: [],
        daily: [],
      },
    },
  },
  isPending: false,
  error: "",
};

export const userDataObj = (state = initUserData, action = {}) => {
  let userData = state.userData;
  let watchlist;
  let event;

  switch (action.type) {
    case USER_DATA_PENDING:
      return { ...state, isPending: true };
    case USER_DATA_SUCCESS:
      return { ...state, isPending: false, userData: action.payload };
    case USER_DATA_FAILED:
      return { ...state, error: action.payload };

    case ADD_WATCHLIST_PENDING:
      return { ...state, isPending: true };
    case ADD_WATCHLIST_SUCCESS:
      createNewWatchlist(userData, action.payload);
      return { ...state, isPending: false };
    case ADD_WATCHLIST_FAILED:
      return { ...state, error: action.payload };

    case DELETE_WATCHLIST_PENDING:
      return { ...state, isPending: true };
    case DELETE_WATCHLIST_SUCCESS:
      deleteWatchlist(userData, action.payload);
      return { ...state, isPending: false };
    case DELETE_WATCHLIST_FAILED:
      return { ...state, error: action.payload };

    case ADD_SYMBOL_PENDING:
      return { ...state, isPending: true };
    case ADD_SYMBOL_SUCCESS:
      watchlist = getWatchlist(userData, action.payload.watchlistName);
      watchlist = watchlist.symbols.push(action.payload.symbol);

      return { ...state, isPending: false };
    case ADD_SYMBOL_FAILED:
      return { ...state, error: action.payload };

    case REMOVE_SYMBOL_PENDING:
      return { ...state, isPending: true };
    case REMOVE_SYMBOL_SUCCESS:
      watchlist = getWatchlist(userData, action.payload.watchlistName);
      removeWatchlistSymbol(watchlist, action.payload.symbol);
      return { ...state, isPending: false };
    case REMOVE_SYMBOL_FAILED:
      return { ...state, error: action.payload };

    case CREATE_EVENT_SUCCESS:
      createEvent(userData, action.payload);
      return { ...state };
    case CREATE_EVENT_FAILED:
      return { ...state, error: action.payload };

    case ACTIVATE_EVENT:
      event = getEvent(userData, action.payload.id);
      event.active = true;
      return { ...state };

    case DEACTIVATE_EVENT:
      event = getEvent(userData, action.payload.id);
      event.active = false;
      return { ...state };

    case DELETE_EVENT_SUCCESS:
      deleteEvent(userData, action.payload.id);
      return { ...state };
    case DELETE_EVENT_FAILED:
      return { ...state, isPending: false, error: action.payload };

    case ACCOUNT_DEPOSIT_SUCCESS:
      accountDeposit(userData, action.payload.deposit);
      return { ...state };
    case ACCOUNT_DEPOSIT_FAILED:
      return { ...state, error: action.payload };

    case DELETE_USER_DATA:
      console.log("DELETING USER DATA");
      return initUserData;

    default:
      return state;
  }
};
