import React, { useState, useEffect } from "react";
import NewsTab from "../PageElements/NewsTab";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import WatchlistContainer from "../PageElements/WatchlistContainer";
import { connect } from "react-redux";
import { setSelectedWatchlistIndex } from "./Watchlist/actions";
import EventsGrid from "../Grids/EventsGrid";
import CustomTab from "../PageElements/CustomTab";

const mapDispatchToProps = (dispatch) => {
  return {
    setSelectedWatchlist: (index) => dispatch(setSelectedWatchlistIndex(index)),
  };
};

const mapStateToProps = (state) => {
  return {
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
    watchlists: state.userDataObj.userData.watchlists,
    events: state.userDataObj.userData.events,
    isUserDataPending: state.userDataObj.isPending,
    selectedWatchlistIndex: state.selectedWatchlistIndex.index,
    isDataRetrieved: Boolean(
      !state.userDataObj.isPending &&
        state.loginStatus.userIsLoggedIn &&
        state.userDataObj.userData.watchlists.length > 0
    ),
  };
};

const WatchlistPage = ({
  watchlists,
  isDataRetrieved,
  selectedWatchlistIndex,
}) => {
  useEffect(() => {
    console.log("selectedWatchlistIndex", selectedWatchlistIndex);
  }, [selectedWatchlistIndex]);

  return (
    <Container maxWidth="lg" disableGutters={true}>
      <Grid item lg>
        <WatchlistContainer />
      </Grid>

      <Grid container direction="row" wrap="nowrap">
        <Grid item zeroMinWidth xs>
          <h2 style={{ borderBottom: "10px double azure" }}>Events</h2>
          <CustomTab tabs={["Price", "Change", "Volume"]}>
            <EventsGrid eventProp="price" />
            <EventsGrid eventProp="change" />
            <EventsGrid eventProp="volume" />
          </CustomTab>
        </Grid>
        <Grid item zeroMinWidth xs>
          <NewsTab
            header
            stockSymbols={
              watchlists.length > 0
                ? watchlists[selectedWatchlistIndex].symbols
                : []
            }
          ></NewsTab>
        </Grid>
      </Grid>
    </Container>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(WatchlistPage);
