import { combineReducers } from "redux";
import { loginState, loginStatus } from "./LoginState/reducer";
import { userDataObj } from "./UserData/reducer";
import { selectedWatchlistIndex } from "../WebPages/Watchlist/reducers";

import { search, requestSearchedStocks } from "./appReducer";

const rootReducer = combineReducers({
  loginState,
  loginStatus,
  userDataObj,
  search,
  requestSearchedStocks,
  selectedWatchlistIndex,
});

export default rootReducer;
