import React, { useState, Component } from "react";
import Search from "../Components/Search";
import Favicon from "../Icons/Favicon";
import { green } from "@material-ui/core/colors";
import Robotrade from "../Icons/Robotrade";
import rticon from "../Icons/rticon.png";
import IconButton from "@material-ui/core/IconButton";
import Avatar from "@material-ui/core/Avatar";
import NotificationsIcon from "@material-ui/icons/Notifications";
import { makeStyles } from "@material-ui/core/styles";
import randomColor from "randomcolor";
import Login from "./Login";
import {
  changeLoginDisplay,
  changeLoginStatus,
} from "../Redux/LoginState/actions";
import { connect } from "react-redux";
import axios from "axios";
import { deleteUserData } from "../Redux/UserData/actions";
import { SERVER_URL } from "../Essential";

axios.defaults.withCredentials = true;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    "& > *": {
      margin: theme.spacing(0),
    },
  },
}));

const mapDispatchToProps = (dispatch) => {
  return {
    setShowLogin: (display) => dispatch(changeLoginDisplay(display)),
    setLoginStatus: (status) => dispatch(changeLoginStatus(status)),
    deleteUserData: () => dispatch(deleteUserData()),
  };
};

const mapStateToProps = (state) => {
  return {
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
    userData: state.userDataObj.userData,
    IsUserDataPending: state.userDataObj.isPending,
  };
};

const Header = ({
  styles,
  setShowLogin,
  isUserLoggedIn,
  setLoginStatus,
  userData,
  isUserDataPending,
  deleteUserData,
}) => {
  const classes = useStyles();
  const color = randomColor({
    luminosity: "bright",
    hue: "random",
  });

  const showUsername = (pending) => {
    if (isUserLoggedIn) {
      return <span>{userData.username}</span>;
    }
  };

  const logout = () => {
    axios.get(`${SERVER_URL}/logout`).then((res) => {
      if (res.status === 200) {
        deleteUserData();
        setLoginStatus(false);
      }
    });
  };
  return (
    <div {...styles}>
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(100pt,auto)) ",
          height: "3rem",
          padding: " 0px 2rem",
          background: "rgb(10, 30, 40)",
          alignItems: "center",
          width: `calc(100vw - ${2 * 2}rem)`,
        }}
      >
        <div style={{ display: "flex", alignItems: "center" }}>
          <Favicon />

          <img height="20" width="140" src={rticon} />
        </div>
        <div className={classes.root}>
          <Avatar
            alt="twoTone"
            style={{ background: color, height: "30px", width: "30px" }}
          />
          {isUserDataPending ? <span></span> : showUsername()}
          <a
            style={{ color: "lightskyblue", cursor: "pointer" }}
            onClick={() => (isUserLoggedIn ? logout() : setShowLogin(true))}
          >
            {isUserLoggedIn ? "Logout" : "Login"}
          </a>
          <IconButton>
            <NotificationsIcon />
          </IconButton>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
          }}
        >
          <Search href="/app/stocks" />
        </div>
      </div>
    </div>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
